video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
    overflow-y: hidden;
    overflow-x: hidden; 
}

.hero-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
}

.hero-container > h2 {
    color: #fff;
    }

.hero-container > p {
margin-top: 8px;
color: #fff;
font-size: 32px;
font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

.hero-btns {
margin-top: 32px;
}

.hero-btns .btn {
margin: 6px;
}

.fa-play-circle {
margin-left: 4px;
}
.heroImage{
    height: 100%;
    width: 100%;
    z-index: -999;
    position:absolute;
}

/* Mobile Media Queries */
@media screen and (max-width: 960px) {
    .hero-container > h1 {
        font-size: 70px;
        margin-top: -150px;
    }
}
    
@media screen and (max-width: 768px) {
    .hero-container > h1 {
        font-size: 50px;
    }
    
    .hero-container > p {
        font-size: 30px;
    }
    
    .btn-mobile {
        display: block;
        text-decoration: none;
    }
    
    .btn {
        width: 100%;
    }
}